"use client";

import { FaFacebookF, FaInstagram } from "react-icons/fa";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

const footerLinks = [
  {
    title: "ABOUT",
    items: ["OUR STORY", "BEYOND CONFLICT FREE", "CUSTOMER REVIEW"],
  },
  {
    title: "POLICIES",
    items: [
      "TERMS & CONDITIONS",
      "PRIVACY POLICY",
      "FAQ",
      "RETURN & REFUND",
      "SHIPPING POLICY",
    ],
  },
  {
    title: "EDUCATION",
    items: [
      "DIAMOND BUYING GUIDE",
      "LAB GROWN DIAMONDS",
      "RING SIZE CHART",
      "JEWELLERY CARE",
      "BLOG",
    ],
  },
];

export default function FooterSection() {
  return (
    <footer className="bg-black text-white px-6 md:px-20 py-12">
      <div className="max-w-7xl mx-auto grid grid-cols-1 md:grid-cols-4 gap-10">
        {/* Brand and Contact Info */}
        <div>
          <h1 className="text-3xl italic font-light">Geer.in</h1>
          <address className="not-italic mt-4 text-sm space-y-2">
            <p>
              2nd floor, Rajhans House, opp. Nayara petrol pump, Varachha road,
              Surat 395006
            </p>
            <p>Mon - Sat from 9:00 AM - 7:00 PM IST</p>
            <p>
              Email:{" "}
              <a href="mailto:contact@geer.in" className="font-semibold hover:underline">
                contact@geer.in
              </a>
            </p>
            <p>
              Phone:{" "}
              <a href="tel:+919773114418" className="font-semibold hover:underline">
                +91 9773114418
              </a>
            </p>
          </address>
          <div className="flex gap-4 mt-4 text-lg">
            <a
              href="https://facebook.com"
              target="_blank"
              rel="noopener noreferrer"
              aria-label="Facebook"
              className="hover:text-blue-500 transition"
            >
              <FaFacebookF />
            </a>
            <a
              href="https://instagram.com"
              target="_blank"
              rel="noopener noreferrer"
              aria-label="Instagram"
              className="hover:text-pink-500 transition"
            >
              <FaInstagram />
            </a>
          </div>
        </div>

        {/* Footer Navigation Columns */}
        {footerLinks.map(({ title, items }) => (
          <div key={title}>
            <h2 className="text-sm font-semibold mb-3">{title}</h2>
            <ul className="space-y-2 text-sm">
              {items.map((item) => (
                <li key={item}>
                  <a href="#" className="hover:text-gray-400 transition">
                    {item}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>

      {/* Newsletter Section */}
      <div className="border-t border-gray-700 mt-12 pt-8">
        <div className="max-w-2xl mx-auto text-center">
          <h2 className="mb-4 text-lg font-medium">Subscribe To Newsletter</h2>
          <form
            onSubmit={(e) => e.preventDefault()}
            className="flex flex-col sm:flex-row items-center gap-2 max-w-md mx-auto"
          >
            <Input
              type="email"
              placeholder="Your Email"
              required
              className="bg-black border-white text-white placeholder:text-gray-400"
            />
            <Button
              type="submit"
              variant="outline"
              className="text-white border-white hover:bg-white hover:text-black"
            >
              Subscribe
            </Button>
          </form>
        </div>
        <p className="text-center mt-8 text-sm text-gray-400">
          © 2025, <span className="font-semibold text-white">Geer</span>.
        </p>
      </div>
    </footer>
  );
}
